import { useMemo } from 'react';
import {
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography
} from '@mui/material';
import dayjs from 'dayjs';
import { useKioskStore } from '../store/useKioskStore';

const STATUS_LABEL: Record<'in' | 'out' | 'auto', string> = {
  in: 'MASUK',
  out: 'PULANG',
  auto: 'AUTO'
};

function formatTimestamp(value?: string): string {
  if (!value) {
    return '-';
  }
  const timestamp = dayjs(value);
  if (!timestamp.isValid()) {
    return '-';
  }
  return `${timestamp.format('YYYY-MM-DD')} | ${timestamp.format('HH:mm:ss')}`;
}

export default function HistoryTable() {
  const history = useKioskStore((state) => state.history);

  const rows = useMemo(() => history.slice(0, 10), [history]);

  return (
    <Box
      sx={{
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        borderRadius: 2,
        bgcolor: 'background.paper',
        border: '1px solid',
        borderColor: 'divider',
        overflow: 'hidden'
      }}
    >
      <Box
        sx={{
          px: 3,
          py: 2,
          borderBottom: '1px solid',
          borderColor: 'divider',
          display: 'flex',
          flexDirection: 'column',
          gap: 0.5
        }}
      >
        <Typography variant="h6" fontWeight={700}>
          Histori Absensi
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Menampilkan 10 data terbaru
        </Typography>
      </Box>
      <TableContainer sx={{ flex: 1 }}>
        <Table stickyHeader size="small" sx={{ minWidth: 520 }}>
          <TableHead>
            <TableRow>
              <TableCell sx={{ width: 64, fontWeight: 700 }}>No</TableCell>
              <TableCell sx={{ fontWeight: 700 }}>Nama</TableCell>
              <TableCell sx={{ fontWeight: 700 }}>Waktu (YYYY-MM-DD | HH:mm:ss)</TableCell>
              <TableCell sx={{ fontWeight: 700 }}>Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.length === 0 ? (
              <TableRow>
                <TableCell colSpan={4}>
                  <Typography variant="body2" color="text.secondary" textAlign="center">
                    Belum ada data absensi
                  </Typography>
                </TableCell>
              </TableRow>
            ) : (
              rows.map((row, index) => (
                <TableRow key={row.id} hover>
                  <TableCell sx={{ fontWeight: 600 }}>{index + 1}</TableCell>
                  <TableCell sx={{ fontWeight: 500 }}>{row.name ?? '-'}</TableCell>
                  <TableCell sx={{ fontFamily: 'monospace' }}>{formatTimestamp(row.tapTime)}</TableCell>
                  <TableCell sx={{ fontWeight: 600 }}>
                    {STATUS_LABEL[row.type] ?? row.type?.toUpperCase() ?? '-'}
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
}

